.App {
  height: 100vh;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.background {
  position: relative;
  height: 100%;
  width: 100%;
  background-image: url('./static/image-6.png');
  background-size: cover;
  background-position: center;
}

.floating-image {
  position: absolute;
  width: 100px; /* Adjust the size of the images */
  height: 100px;
  animation: float 10s infinite ease-in-out;
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0px);
  }
}

/* Position each image at different locations */
.floating-image:nth-child(1) {
  top: 10%;
  left: 20%;
}

.floating-image:nth-child(2) {
  top: 30%;
  left: 50%;
}

.floating-image:nth-child(3) {
  top: 50%;
  left: 10%;
}

.floating-image:nth-child(4) {
  top: 70%;
  left: 40%;
}

.floating-image:nth-child(5) {
  top: 20%;
  left: 80%;
}

.floating-image:nth-child(6) {
  top: 60%;
  left: 70%;
}

